.container_for__icon-coffee {
  width: 30px;
  height: 30px;
  border-radius: 100px;
  background-color: #e1d4c9;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon__item-desert,
.icon__item-tea,
.icon__item-coffee {
  cursor: pointer;
  display: flex;
  height: 46px;
  align-items: center;
  border: 1px solid #c1b6ad;
  justify-content: space-evenly;
  border-radius: 100px;
  transition: all 0.5s ease;
}

.icon__item-coffee {
  cursor: default;
}

.icon__item-coffee {
  border-radius: 100px;
  background-color: #665f55;
}

.container_for__icon-tea,
.container_for__icon-dessert {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border-radius: 100px;
  background-color: #c1b6ad;
  transition: all 0.5s ease;
}

.icon__item-tea {
  width: 90px;
  margin: 0px 17px 0 13px;
}

.icon__item-coffee {
  width: 115px;
}

.icon__item-desert {
  width: 124px;
}

.icon__item-text-tea {
  margin-right: 5px;
}

.icon__item-text-dessert {
  margin-right: 11px;
}

.icon__item-text-coffee {
  color: #e1d4c9;
  margin-right: 6px;
  color: #e1d4c9;
}

.icon__item-tea:hover {
  color: #e1d4c9;
  background-color: #665f55;
  color: #e1d4c9;
}
.icon__item-tea:hover .container_for__icon-tea {
  transition: all 0.5s ease;
  background-color: #e1d4c9;
}

.icon__item-desert:hover {
  background-color: #665f55;
  color: #e1d4c9;
}
.icon__item-desert:hover .container_for__icon-dessert {
  transition: all 0.5s ease;
  background-color: #e1d4c9;
}

@media (max-width: 380px) {
  .icon__item-coffee {
    width: 115px;
  }
  .icon__item-tea {
    margin: 0 10px 0 3px;
  }
  .menu__icons-container {
    justify-content: space-between;
  }
}
a {
  color: #403f3d;
  -webkit-text-decoration: none;
  text-decoration: none;
}

.menu__link {
  cursor: default;
}

.nav__item-link {
  position: relative;
  transition: all 0.5s ease;
}

.nav__item-link:after {
  display: block;
  position: absolute;
  content: "";
  height: 2px;
  bottom: 0;
  left: 50%;
  width: 0;
  background-color: #403f3d;
  transition: all 0.7s;
}

.nav__item-link::before {
  display: block;
  position: absolute;
  content: "";
  height: 2px;
  bottom: 0px;
  right: 50%;
  width: 0;
  background-color: #403f3d;
  transition: all 0.7s;
}

.footer__link {
  position: relative;
  color: #e1d4c9;
  transition: all 0.5s ease;
}

.footer__link::after {
  display: block;
  position: absolute;
  content: "";
  height: 2px;
  bottom: 0;
  right: 70%;
  width: 0;
  background-color: #e1d4c9;
  transition: all 0.7s;
}

.footer__link::before {
  display: block;
  position: absolute;
  content: "";
  height: 2px;
  bottom: 0px;
  left: 30%;
  width: 0;
  background-color: #e1d4c9;
  transition: all 0.7s;
}

.footer__link:hover::after {
  width: 30%;
}

.footer__link:hover::before {
  width: 50%;
}

.burger__btn {
  display: none;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  margin-top: 8px;
  width: 42px;
  height: 42px;
  border: 1px solid #665f55;
  border-radius: 100px;
}

.burger__line {
  width: 18px;
  height: 1.5px;
  background-color: #665f55;
  transition: all 1s ease;
}

.burger__line:nth-child(1) {
  margin-bottom: 6px;
}

.burger__menu {
  opacity: 0;
  position: absolute;
  background-color: #e1d4c9;
  width: 5%;
  right: 0;
  transition: all 2s ease;
  height: 482px;
}

.burger__list {
  padding: 0;
  font-size: 16;
  font-weight: 600;
  line-height: 24px;
  font-style: normal;
  font-family: "Inter", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.burger__item {
  padding-bottom: 60px;
  list-style-type: none;
  display: none;
}

@media (max-width: 768px) {
  .burger__btn {
    display: flex;
  }
}
.--active-burger {
  opacity: 1;
  right: 0;
  width: 100vw;
}
.--active-burger .burger__item {
  display: block;
}

.--active-btn-burger {
  transition: all 1s ease;
}

.burger__btn.--active-btn-burger .burger__line:nth-child(1) {
  transition: all 1s ease;
  transform: translateY(4px) rotate(-45deg);
}
.burger__btn.--active-btn-burger .burger__line:nth-child(2) {
  transition: all 1s ease;
  transform: translateY(-3px) rotate(45deg);
}

.menu__reboot {
  display: none;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 60px;
  height: 60px;
  border: 1px solid #665f55;
  border-radius: 100px;
  margin-bottom: 100px;
}

@media (max-width: 768px) {
  .menu__reboot {
    display: flex;
  }
}
.coffee__slider {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

@media (hover: hover) {
  .left-container__img-item:hover {
    background-color: #e1d4c9;
  }
  .left-container__img-item:hover .left-container__img-svg {
    stroke: #403f3d;
  }
  .icon__item-tea:hover {
    background-color: #665f55;
  }
  .icon__item-tea:hover .container_for__icon-tea {
    background-color: #e1d4c9;
  }
  .icon__item-desert:hover {
    background-color: #665f55;
  }
  .icon__item-desert:hover .container_for__icon-dessert {
    background-color: #e1d4c9;
  }
  .arrow-left:hover {
    background: #665f55;
  }
  .arrow-left:hover .arrow-left__svg {
    stroke: #e1d4c9;
  }
  .arrow-right:hover {
    background: #665f55;
  }
  .arrow-right:hover .arrow-right__svg {
    stroke: #e1d4c9;
  }
  .enjoy__btn_meny:hover .enjoy__btn_text {
    right: 98px;
  }
  .enjoy__btn_meny:hover::after {
    transition: all 0.7s ease;
    opacity: 1;
  }
  .img-container__item:hover {
    transform: scale(1);
  }
  .nav__item-link:hover::before,
  .nav__item-link:hover::after {
    width: 50%;
  }
  .footer__link:hover::after {
    width: 30%;
  }
  .footer__link:hover::before {
    width: 45%;
  }
  .menu__text-home:hover::before {
    width: 50%;
  }
  .menu__text-home:hover::after {
    width: 80%;
  }
  .menu__drinks-item:hover .menu__drinks-img {
    transform: scale(1);
  }
}
.nav__item-link:active,
.footer__link:active {
  opacity: 0.5;
}

